@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

:root {
    --dark-black: #000000;
    --white-color: #fff;
    --monstrate-font-500: 'Montserrat', sans-serif;
    --history-btn-color: #2C2D30;
    --green-color: #28A909;
    --auto-btn-color: #C86325;
    --controllers-bg-color: rgb(27 21 54);
    --playbet-btn-border: #B2F2A3;
}

.gameboard-section {
    width: 100%;
}

.gameboard-section .gameboard-container {
    width: 95%;

    margin: auto;
    display: flex;
    justify-content: space-between;

}

.gameboard-section .gameboard-container .game-section {
    width: 70%;

    max-width: 73%;

}

.gameboard-section .gameboard-container .game-section .game-history-header {
    width: 100%;
    display: flex;
}

.gameboard-section .gameboard-container .game-section .game-history-header .histories {
    max-width: 90%;
    display: flex;
    align-items: center;
    list-style: none;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 10px;
}



.gameboard-section .gameboard-container .game-section .game-history-header .histories .history {
    color: #F8C961;
    padding: 0px 20px;
    font-size: 0.8rem;
    font-family: var(--monstrate-font-500);
}

.gameboard-section .gameboard-container .game-section .game-history-header .history-btn {
    width: 60px;
    height: 30px;
    cursor: pointer;
    margin-left: 10px;
    margin-top: -5px;
}

.gameboard-section .gameboard-container .game-section .game-history-header .history-btn img {
    width: 100%;
    height: 100%;
}

/* Response breakpoint */
@media screen and (max-width:820px) {
    .gameboard-section {
        width: 100%;
        height: 100%;

    }

    .gameboard-section .gameboard-container {
        width: 95%;
        margin: auto;
        display: block;

    }

    .gameboard-section .gameboard-container .game-section {
        width: 100%;
        max-width: 100%;
        margin-top: 10px;
        padding-left: 0px;
    }

    .gameboard-section .gameboard-container .game-section .game-history-header .histories {
        max-width: 85%;
        display: flex;
        align-items: center;
        list-style: none;
        overflow-x: auto;
        scroll-snap-type: x mandatory;
        -webkit-overflow-scrolling: touch;
        padding-bottom: 5px;
    }


    .gameboard-section .gameboard-container .game-section .game-history-header .history-btn {
        width: 40px;
        height: 25px;
        cursor: pointer;
        margin-left: 10px;
        margin-top: -5px;
    }

    /* unity game box designe */
    .gameboard-section .gameboard-container .game-section .unity-game-box {
        width: 100%;
        height: 45%;
        margin: auto;
        margin-top: 10px;
    }

    .gameboard-section .gameboard-container .game-section .unity-game-box .canvas {
        width: 100%;
        height: 100%;
        background-color: var(--white-color);
    }

}

/* unity game box designe */
.unity-game-box {
    width: 100%;
    height: 57vh;
    margin-top: 7px;


}

.unity-game-box .canvas {
    width: 100%;
    height: 100%;


}

.unity-game-box .canvas .unity {
    width: 100%;
    height: 100%;
    background-color: var(--white-color);
}

/* Game controllers  */

.gameboard-section .gameboard-container .game-section .game-controllers-section {
    width: 100%;
    height: 23vh;
    margin-top: 12px;

}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container {
    width: 90%;
    height: 23vh;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers {
    width: 44%;
    height: 22vh;
    background-color: var(--controllers-bg-color);
    border-radius: 12px;
    padding: 10px;
    padding-bottom: 0px;
    align-items: center;

}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .header-buttons {
    width: 100%;
    text-align: center;
}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .header-buttons button {
    width: 35%;
    padding: 5px 0px 5px 0px;
    margin: auto;
    border-radius: 20px;
    background-color: var(--history-btn-color);
    color: var(--white-color);
    border: none;
    font-family: var(--monstrate-font-500);
    text-transform: capitalize;
    font-size: 0.9rem;
    margin-top: 10px;
    cursor: pointer;
}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .header-buttons .auto-btn {
    background-color: var(--auto-btn-color);
}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .play-bet-controllers {
    width: 90%;
    margin: auto;
    justify-content: space-around;
    display: flex;
    margin-top: 10px;
    align-items: center;

}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .play-bet-controllers .apply-bet-controllers {
    width: 38%;
}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .play-bet-controllers .apply-bet-controllers .bet-counter {
    width: 100%;
    height: 23px;
    padding: 2px;
    margin: auto;
    border-radius: 20px;
    background-color: var(--history-btn-color);
    color: var(--white-color);
    border: none;
    font-family: var(--monstrate-font-500);
    text-transform: capitalize;
    font-size: 0.9rem;
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;


}

.gameboard-section .gameboard-container .game-section .game-controllers-section .gamecontrollers-container .left-controllers .play-bet-controllers .apply-bet-controllers .bet-counter button {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: none;
    border: 1px solid #ffffff;
    font-size: 1rem;
    background: transparent;
    color: var(--white-color);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px;
