/* bets details section designe */
.gamebets-section {
    width: 30%;
    margin-left: 30px;
    margin-top: 40px;
}


.gamebets-section .gamebets-tabs {
    display: flex;
    align-items: center;
}

.gamebets-section .gamebets-tabs .active-tab {
    color: var(--white-color);
    background-color: var(--auto-btn-color);

}

.gamebets-section .gamebets-tabs button {
    padding: 4px 26px 4px 26px;
    margin: auto;
    border-radius: 20px;
    background-color: var(--history-btn-color);
    color: var(--white-color);
    border: none;
    font-family: var(--monstrate-font-500);
    text-transform: capitalize;
    font-size: 0.7rem;
    cursor: pointer;
    transition: ease all 0.5s;
}

.gamebets-section .gamebets-details .details-header {
    display: flex;
    justify-content: space-between;
}

.gamebets-section .gamebets-details .details-header .total-bets {
    font-family: var(--monstrate-font-500);
    color: var(--white-color);
    padding-top: 10px;
    margin-left: 13px;
    font-size: 0.8rem;
    display: block;
}

.gamebets-section .gamebets-details .details-header .previous-hand button {
    padding: 4px 20px 4px 20px;
    margin: auto;
    border-radius: 20px;
    background-color: var(--history-btn-color);
    color: var(--white-color);
    border: none;
    font-family: var(--monstrate-font-500);
    text-transform: capitalize;
    font-size: 0.7rem;
    margin-top: 10px;

}

.gamebets-section .gamebets-details .details-headings {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}

.gamebets-section .gamebets-details .details-headings div {
    color: var(--white-color);
}

.gamebets-section .gamebets-details .details-details {
    width: 95%;
    height: 65vh;
    margin: auto;
    overflow-y: auto;
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
}

.gamebets-section .gamebets-details .details-details::-webkit-scrollbar {
    height: 0.2rem;
    width: 0.5rem;
}

.gamebets-section .gamebets-details .details-details::-webkit-scrollbar-thumb {
    background-color: var(--controllers-bg-color);
    border-radius: 20px;
}

.gamebets-section .gamebets-details .details-details table {
    width: 100%;
    border: none;
    border-collapse: collapse;
    margin-top: 20px;

}



.gamebets-section .gamebets-details .details-details table th {
    color: var(--white-color);
    font-family: var(--monstrate-font-500);
    font-size: 0.8rem;
    text-transform: capitalize;
    font-weight: 400;
}

.gamebets-section .gamebets-details .details-details table tr td {
    color: var(--white-color);
    font-family: var(--monstrate-font-500);
    font-size: 0.8rem;
    text-align: center;
    padding-top: 15px;
}



