@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

:root {
    --dark-black: #000000;
    --white-color: #fff;
    --monstrate-font-500: 'Montserrat', sans-serif;
    --history-btn-color: #2C2D30;
    --green-color:#28A909;
    --auto-btn-color:#C86325;
    --controllers-bg-color:#1B1C1D;
}

 table{
    width: 100%;
    border: none;
    border-collapse: collapse;
    margin-top: 10px;
   padding-left: 10px;
  
}
table.date-based-histories{
    max-width: 70%;
   
    margin-top: 0px;
   
}
 table th{
    color: var(--white-color);
    font-family: var(--monstrate-font-500);
    font-size: 0.8rem;
    text-transform: capitalize;
    font-weight: 400;
}

table tbody tr td{
    color: var(--white-color);
    font-family: var(--monstrate-font-500);
    font-size: 0.8rem;
    text-align: center;
    padding-bottom: 5px;

}
 tr td .user{
    display: flex;
    align-items: center;
   color: var(--white-color);
}
 tr td .user img{
    width: 30px;
    height: 30px;
    clip-path: circle();
    margin-right: 12px;
}

.top-tabs {
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.top-tabs button {
    padding: 4px 26px 4px 26px;
    margin: auto;
    border-radius: 20px;
    background-color: var(--history-btn-color);
    color: var(--white-color);
    border: none;
    font-family: var(--monstrate-font-500);
    text-transform: capitalize;
    font-size: 0.7rem;
    cursor: pointer;
    margin-inline: 4px;
}
.top-tabs .active-tab{
    background: var(--auto-btn-color);
    color: var(--white-color);
    transition: ease all 0.5s;
}
.bets-empty-message{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    color: var(--white-color);
    font-size: 0.8rem;
    text-transform: capitalize;
    font-family: var(--monstrate-font-500);
    font-weight: 400;
    color: var(--auto-btn-color);
}